.container {
  min-width: 384px;
  max-width: 414px;
  margin: 0 auto;
  padding: 0 15px;

  @media screen and (min-width: 1024px) {
    width: 100%;
    max-width: 1024px;
    padding: 0 47px;
  }

  @media screen and (min-width: 1280px) {
    width: 100%;
    max-width: 1280px;
    padding: 0 85px;
  }
}
